<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="crumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{castId}}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="search_bar" :model="searchForm" :inline="true">
      <el-form-item label="">
        <el-date-picker
          v-model="searchForm.date"
          type="daterange"
          :default-time="['00:00:00', '23:59:59']"
          :clearable="false"
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.caster" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.furnace" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="clickSearch"></el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="reset"></el-button>
      </el-form-item>
    </el-form>
    <div class="main_bd">
      <el-col class="table_hd">
        <el-button type="primary" icon="el-icon-plus" @click="add"></el-button>
      </el-col>
      <el-table :data="tableData" stripe border style="width:100%" v-loading="loading">
        <el-table-column type="expand" label="" width="50px">
          <template slot-scope="props">
            <!--  -->
            <el-table
            :data="props.row.record" stripe border style="width:100%">
              <el-table-column label="" type="index" width="50px"></el-table-column>
              <el-table-column prop="nozzleSize" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="treatCoolRoller" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="coolRollerThickness" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="ReceiveMeltTime" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="tundishTemperatureWithoutMelt" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="tundishTemperatureWithMelt" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="installNozzleTime" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="castTimeStart" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="pressure" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="tundishTemperatureCasting" label="/" align="center" width="100px"></el-table-column>
              <el-table-column prop="coolRollerTemperatureBeforeCast" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="coolRollerTemperatureAfterCast" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="castLocation" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="coilTimes" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="castTimeEnd" label="" align="center" width="110px"></el-table-column>
              <el-table-column prop="describe" label="" align="center" width="110px" :show-overflow-tooltip="true"></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column prop="createdAt" label="" align="center" width="110px" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="ribbonTypeName" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="ribbonWidth" label="" align="center" width="50px"></el-table-column>
        <el-table-column prop="furnace" label="" align="center" width="170px"></el-table-column>
        <el-table-column prop="caster" label="" align="center" width="70px"></el-table-column>
        <el-table-column prop="tundish" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="tundishCar" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="isChangeTundish" label="" align="center" width="80px">
          <template slot-scope="scope">
            <span v-if="scope.row.isChangeTundish === 1"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="nozzleNum" label="" align="center" width="100px"></el-table-column>
        <el-table-column prop="heatCupNum" label="" align="center" width="100px"></el-table-column>
        <el-table-column prop="meltOutWeight" label="(kg)" align="center" width="110px"></el-table-column>
        <el-table-column prop="rawWeight" label="(kg)" align="center" width="110px"></el-table-column>
        <el-table-column prop="remark" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="updatedAt" label="" align="center" width="170px" :formatter="dateTimeFormat"></el-table-column>
        <el-table-column label="" align="center" width="150px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="edit(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="del(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="total,prev,pager,next"
        :total="pageConfig.total"
        :current-page.sync="pageConfig.current"
        :page-size="pageConfig.pageSize"
        @current-change="handleCurrentChange"></el-pagination>
    </div>
    <dialog-form v-if="dialogVisible" :dialogData="{ formType, dialogVisible, rowData }" @close="closeHandler" @submit="submitHandler"></dialog-form>
  </div>
</template>

<script>
import urlmap from '@/utils/urlmap';
import { dateFormat, dateTimeFormat } from '@/utils/common';
import dialogForm from './components/dialogForm.vue';

export default {
  name: 'cast',
  components: {
    dialogForm
  },
  data () {
    return {
      castId: 6,
      searchForm: {
        caster: '',
        furnace: '',
        date: []
      },
      loading: false,
      tableData: [],
      dialogVisible: false,
      formType: 'create',
      rowData: {},
      pageConfig: {
        total: 1,
        current: 1,
        pageSize: 10
      }
    }
  },
  // 
  beforeRouteUpdate(to, from, next) {
    this.castId = to.params.castId;
    this.getTableData();
    next();
  },
  created () {
    this.getTableData();    
  },
  methods: {
    dateFormat(row, column) {
      return dateFormat(row.createdAt);
    },
    dateTimeFormat(row, column) {
      return dateTimeFormat(row.updatedAt);
    },
    clickSearch() {
      // 1
      const params = {
        current: 1
      };
      this.pageConfig.current = 1;
      this.getTableData(params);
    },
    reset() {
      this.searchForm = { caster: '', furnace: '',  date: [] };
      const params = {
        current: 1
      };
      this.pageConfig.current = 1;
      this.getTableData(params);
    },
    getTableData(params = {}) {
      const _params = {
        castId: this.castId,
        startTime: this.searchForm.date[0],
        endTime: this.searchForm.date[1],
        caster: this.searchForm.caster,
        furnace: this.searchForm.furnace
      };
      Object.assign(params, _params);
      this.$http('get', urlmap.queryCast, params).then(data => {
        this.pageConfig.total = data.count;
        this.tableData = data.list;
      }).catch((err) => {
        console.log(err);
      }).finally(() => {
        this.loading = false;
      });
    },
    add() {
      this.dialogVisible = true;
      this.formType = 'add';
    },
    edit(row) {
      this.rowData = row;
      this.dialogVisible = true;
      this.formType = 'edit';
    },
    del(row) {
      const { _id, furnace } = row;
      this.$confirm(` ${furnace} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http('delete', urlmap.delCast, {_id}).then(data => {
          this.getTableData();
        }).catch(error => {
          console.log(error);
        });
      }).catch(() => {});
    },
    closeHandler() {
      this.dialogVisible = false;
    },
    submitHandler() {
      this.dialogVisible = false;
      this.pageConfig.current = 1;
      this.getTableData();
    },
    handleCurrentChange(val) {
      const params = {
        current: val
      };
      this.getTableData(params);
    }
  }
}
</script>

<style lang="scss" scoped>

</style>




