<template>
  <el-dialog 
  :title="dialogData.formType === 'create' ? `-${$route.params.castId}` : `-${$route.params.castId}`" 
  :visible.sync="dialogData.dialogVisible" 
  :close-on-click-modal="false"
  :close-on-press-escape="false" 
  @close="closeDialog"
  :center="true"
  width="950px"
  v-loading="loading"
  element-loading-text="">
    <el-form :model="formData" :rules="rules" ref="form" label-width="100px;" style="width:100%" @submit.native.prevent inline>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="" prop="date" class="dialog_field">
            <el-date-picker v-model="formData.date" type="date" value-format="yyyy-MM-dd" placeholder="" :editable="false" :clearable="false"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="castId" class="dialog_field">
            <el-select v-model="formData.castId" placeholder="" disabled>
              <el-option label="6" :value="6"></el-option>
              <el-option label="7" :value="7"></el-option>
              <el-option label="8" :value="8"></el-option>
              <el-option label="9" :value="9"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="team" class="dialog_field">
            <el-select v-model="formData.team" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="" prop="taskOrder" class="dialog_field">
            <el-input v-model="formData.taskOrder"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="ribbonTypeId" class="dialog_field">
            <el-select v-model="formData.ribbonTypeId" placeholder="">
              <el-option v-for="item in ribbonTypeList" :key="item.ribbonTypeId" :label="item.ribbonTypeName" :value="item.ribbonTypeId"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="(mm)" prop="ribbonWidth" class="dialog_field">
            <el-input v-model="formData.ribbonWidth"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="" prop="client" class="dialog_field">
            <el-input v-model="formData.client"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="(m)" prop="thickness" class="dialog_field">
            <el-input v-model="formData.thickness"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="laminationFactor" class="dialog_field">
            <el-input v-model="formData.laminationFactor"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="" prop="furnace" class="dialog_field">
            <el-input v-model="formData.furnace"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="alloyWeight" class="dialog_field">
            <el-input v-model="formData.alloyWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="castTime" class="dialog_field">
            <el-input v-model="formData.castTime"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="" prop="fileNumber" class="dialog_field">
            <el-input v-model="formData.fileNumber"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="16">
          <el-form-item label="" prop="remark" class="dialog_field orign_field"> 
            <el-input v-model="formData.remark"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer">
      <el-button @click="closeDialog"></el-button>
      <el-button type="primary" @click="submitForm"></el-button>
    </div>
  </el-dialog>  
</template>

<script>
import { positiveInteger, ltNumber } from '@/utils/validate';
import urlmap from '@/utils/urlmap';
import { mapState, mapActions } from 'vuex';

const formConfig = {
  date: '',
  castId: 6,
  team: '',
  taskOrder: '',
  ribbonTypeId: '',
  ribbonTypeName: '',
  ribbonWidth: 30,
  client: '',
  thickness: '',
  laminationFactor: '',
  furnace: '',
  alloyWeight: 230,
  castTime: '',
  rawWeight: '',
  remark: '12',
  fileNumber: ''
};

export default {
  props: {
    dialogData: {
      type: Object,
      required: true
    }
  },
  data() {
    const checkFurnance = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }

      const reg = /^0[1-9]-[0-9]{8}-[0-9]{2}$/;
      if (reg.test(value)) {
        callback();
      } else {
        callback(new Error(''));
      }
    };

    return {
      visible: false,
      loading: false,
      formData: {
        date: '',
        castId: 6,
        team: '',
        taskOrder: '',
        ribbonTypeId: '',
        ribbonTypeName: '',
        ribbonWidth: 30,
        client: '',
        thickness: '',
        laminationFactor: '',
        furnace: '',
        alloyWeight: 230,
        castTime: '',
        rawWeight: '',
        remark: '',
        fileNumber: ''
      },
      rules: {
        date: [{ required: true, message: '', trigger: 'blur' }],
        castId: [{ required: true, message: '', trigger: 'blur' }],
        team: [{ required: true, message: '', trigger: 'blur' }],
        ribbonTypeId: [{ required: true, message: '', trigger: 'blur' }],
        ribbonWidth: [
          { required: true, message: '', trigger: 'blur' },
          { validator: positiveInteger, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        client: [{ max: 50, message: '50', trigger: 'blur' }],
        thickness: [
          { required: true, message: '', trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' },
        ],
        laminationFactor: [
          { required: true, message: '', trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' }
        ],
        furnace: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          { validator: checkFurnance, trigger: 'blur'}
        ],
        alloyWeight: [
          { validator: positiveInteger, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }   
        ],
        castTime: [{ max: 50, message: '50', trigger: 'blur' }],
        remark: [{ max: 100, message: '100', trigger: 'blur' }],
        fileNumber: [{ max: 50, message: '50', trigger: 'blur' }]
      }
    }
  },
  computed: {
    ...mapState([
      'ribbonTypeList'
    ])
  },
  created () {
    if (this.dialogData.formType === 'create') {
      this.formData = Object.assign({}, formConfig, { castId: Number(this.$route.params.castId) });
    } else {
      this.formData = Object.assign(this.formData, this.dialogData.rowData);
    }
    this.getRibbonTypeList();
  },
  methods: {
    ...mapActions([
      'getRibbonTypeList'
    ]),
    closeDialog() {
      this.$emit('close');
    },
    submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true;
          this.formData.ribbonTypeName = this.ribbonTypeList && this.ribbonTypeList.find(item => {
            return item.ribbonTypeId === this.formData.ribbonTypeId;
          }).ribbonTypeName;

          const { method, url } = this.dialogData.formType === 'create' ? { method: 'post', url: urlmap.addPlan } : { method: 'put', url: urlmap.updatePlan } ;

          this.$http(method, url, this.formData).then(data => {
            this.$emit('submit');
          }).catch(err => {
            console.log(err);
          }).finally(() => {
            this.loading = false;
          });    
        } else {
          return false;
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>

</style>





